/*
  Parts
*/

.btn {
  display: block;
  border-radius: 9999px;
  font-size: 1.6rem;
  padding: 0.8rem 2.4rem;
  border-width: 2px;
  border-style: solid;
  max-width: 240px;
  min-width: 160px;
  cursor: pointer;
  transition: 0.2s ease all;
  text-align: center;
  margin-right: 1.6rem;
  &:last-child {
    margin-right: 0;
  }

  &--accent {
    border-color: $accent-color;
    background: $accent-color;
    color: $white;
    &:hover {
      background: transparent;
      color: $accent-color;
    }
  }

  &--secondary {
    border-color: $secondary-color;
    background: $secondary-color;
    color: $white;
    &:hover {
      background: transparent;
      color: $secondary-color;
    }
  }

  &--cancel {
    background: transparent;
    border-color: #aaa;
    color: #aaa;
    &:hover {
      background: #aaa;
      color: $white;
    }
  }
}

.box {
  padding: 3.2rem;
  background: $white;
  border-radius: 0.8rem;
  &--post {
    padding: 3.2rem 3.2rem 4.8rem;
  }
}

.page-title {
  font-size: 2.4rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 2.4rem;
}

.title {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 2.4rem;
}

.tag {
  display: inline-block;
  background: skyblue;
  font-size: 1.2rem;
  line-height: 1;
  color: $white;
  padding: 0.6rem 1.2rem;
  margin: 0 0.4rem 0.4rem 0;
  border-radius: 9999px;

  &--travel {
    background: #00BFB2;
  }

  &--fashion {
    background: #ffc95e;
  }

  &--entertainment {
    background: #F79563;
  }

  &--sports {
    background: #F25D5A;
  }

  &--political-economy {
    background: #006AD1;
  }

  &--money {
    background: #79C362;
  }

  &--it {
    background: #a453d9;
  }
}

.toast {
  // position: fixed;
  // left: 50%;
  // bottom: 3rem;
  // visibility: hidden;
  width: 320px;
  font-size: 1.2rem;
  color: #fff;
  text-align: center;
  border-radius: 4px;
  padding: 1.2rem;
  z-index: 1;
  margin: 0 auto 1.2rem;

  &--info {
    background: #39991f;
    position: fixed;
    left: 50%;
    bottom: 3rem;
    z-index: 1;
    visibility: hidden;
    transform: translate(-50%, 0);
    &.active {
      visibility: visible;
      animation: fadein 0.5s, fadeout 0.5s 3.5s;
    }
  }
  &--danger {
    background: #e92f2f;
  }
}
